/**
 * AddToCalendar Base CSS
 * http://addtocalendar.com
 */

/* Base */

.addtocalendar var{
  display: none;
}

.addtocalendar {
  position: relative;
  display: inline-block;
  background: transparent!important;
}

.atcb-link {
  display: block;
  outline: none!important;
  cursor: pointer;
}

.atcb-link:focus~ul,
.atcb-link:active~ul,
.atcb-list:hover{
  visibility:visible;
}

.atcb-list {
  visibility: hidden;
  position: absolute;
  top: 100%;
  left: 0;
  width: 170px;
  z-index: 900;
}

.atcb-list,
.atcb-item
{
  list-style: none;
  margin: 0;
  padding: 0;
}

.atcb-item {
  float: none;
  text-align: left;
}

.atcb-item-link
{
  text-decoration: none;
  outline: none;
  display: block;
}

.atcb-item.hover,
.atcb-item:hover {
  position: relative;
  z-index: 900;
  cursor: pointer;
  text-decoration: none;
  outline: none;
}



ul.atcb-list {
  list-style: none;
  li {
    margin-bottom: em(11, 16);
    padding-left: em(23, 16);
    background-image: url("../svg/arrow_right.svg");
    background-position: 0px 50%;
    background-size: em(11, 16);
    background-repeat: no-repeat;
    //line-height: 140%;
  }
}
